import { create } from 'zustand';
import { EDUCATION_LEVELS, QUIZ_TYPES } from '../models/level.js';

/**
 * Level Store
 * Quản lý state của level, subject, quiz type selection
 */
export const useLevelStore = create((set, get) => ({
  // Current selections
  currentLevel: null,
  currentSubject: null,
  currentQuizType: null,
  
  // Filters
  filterSubject: null,      // Filter theo môn (có thể null)
  filterQuizType: null,      // Filter theo loại (có thể null)
  
  // Quiz list
  quizList: [],
  loadingQuizList: false,
  
  // Actions
  setCurrentLevel: (level) => {
    set({ 
      currentLevel: level,
      currentSubject: null,
      currentQuizType: null,
      quizList: [],
    });
  },
  
  setCurrentSubject: (subject) => {
    set({ 
      currentSubject: subject,
      currentQuizType: null,
      quizList: [],
    });
  },
  
  setCurrentQuizType: (quizType) => {
    set({ 
      currentQuizType: quizType,
      quizList: [],
    });
  },
  
  setFilterSubject: (subjectId) => {
    set({ filterSubject: subjectId });
  },
  
  setFilterQuizType: (quizType) => {
    set({ filterQuizType: quizType });
  },
  
  setQuizList: (quizzes) => {
    set({ quizList: quizzes });
  },
  
  setLoadingQuizList: (loading) => {
    set({ loadingQuizList: loading });
  },
  
  // Clear all
  clearSelection: () => {
    set({
      currentLevel: null,
      currentSubject: null,
      currentQuizType: null,
      filterSubject: null,
      filterQuizType: null,
      quizList: [],
    });
  },
  
  // Clear filters only
  clearFilters: () => {
    set({
      filterSubject: null,
      filterQuizType: null,
    });
  },
}));



